import React, {Fragment, useState, useRef, createRef} from 'react';

const InputField = React.forwardRef((props : {state, setState}, ref : React.RefObject<HTMLInputElement>) => {
	const state = props.state;
	const setState = props.setState;
	function updateExplosion(str){
		const thaivalid = /[\u0E01-\u0E30]|\u0E32|[\u0E3F-\u0E46]|[\u0E50-\u0E5B]/;
		const thaicheck = /[\u0E00-\u0E7F]/;
		const outs = [''];
		for(var i = 0; i < str.length; i++){
			if(!thaicheck.test(str[i]) || thaivalid.test(str[i])){
				outs.push(' ');
			}
			outs.push(str[i]);
		}
		setState({...state, explosion: outs.join('').slice(1)});
	}
	return <Fragment>
		<input type="text" placeholder="ใส่ข้อความที่นี่" value={state.txt} ref={ref} onChange={(e) => {
			setState({
				...state,
				txt: e.target.value
			});
			updateExplosion(state.txt)
		}}/>
		<style jsx>
		{`
			input {
				font-size: 14pt;
				line-height: 2;
			}
		`}
		</style>
	</Fragment>;
});

function OutputExplosion({state, setState}){
	return <Fragment>
		<pre>
			{state.explosion}
		</pre>
		<style jsx>
			{`
				pre {
					font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
					font-size: 20pt;
				}
			`}
		</style>
	</Fragment>
}

function Index() {
	const [state, setState] = useState({txt: '', explosion: '', copySuccess: ''});
	const textAreaRef = createRef<HTMLInputElement>();
	const copyToClipboard = (e) => {
		textAreaRef.current.select();
		document.execCommand('copy');
		e.target.focus();
		setState({...state, copySuccess: 'Copied!'});
	}
	return (
	  <div className="mainBox">
		<h1>Exploder</h1>
		<InputField state={state} setState={setState} ref={textAreaRef} />
		<OutputExplosion state={state} setState={setState} />
		{
			document.queryCommandSupported('copy') &&
			<button onClick={(e) => copyToClipboard(e)}>
				Copy to clipboard
			</button>
		}
		<br />
		{state.copySuccess}
		<br />
		<br />
		<p>by Sirawit Pongnakintr</p>
		<p>Last Updated: 21 Feb</p>
		<style jsx>{`
			h1 {
				font-family: 'Tahoma', 'Arial';
				font-size: 36pt;
			}
			.mainBox {
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
			}
			p {
				font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
				font-size: 10pt;
				line-height: 0.1;
			}
		`}</style>
	  </div>
	);
  }

export default Index;